// src/taskpane/fsAccess.ts
// Persist a user-chosen folder; reprompt if file missing.

const DB_NAME = "docImporterDb";
const STORE = "handles";
const KEY = "imports_root";

let currentDir: FileSystemDirectoryHandle | null = null;

// IndexedDB helpers
function idbOpen(): Promise<IDBDatabase> {
  return new Promise((resolve, reject) => {
    const req = indexedDB.open(DB_NAME, 1);
    req.onupgradeneeded = () => req.result.createObjectStore(STORE);
    req.onsuccess = () => resolve(req.result);
    req.onerror = () => reject(req.error);
  });
}
async function idbGet<T>(key: string): Promise<T | undefined> {
  const db = await idbOpen();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE, "readonly");
    const req = tx.objectStore(STORE).get(key);
    req.onsuccess = () => resolve(req.result as T | undefined);
    req.onerror = () => reject(req.error);
  });
}
async function idbSet<T>(key: string, val: T): Promise<void> {
  const db = await idbOpen();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE, "readwrite");
    const req = tx.objectStore(STORE).put(val as any, key);
    req.onsuccess = () => resolve();
    req.onerror = () => reject(req.error);
  });
}
async function idbDel(key: string): Promise<void> {
  const db = await idbOpen();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE, "readwrite");
    const req = tx.objectStore(STORE).delete(key);
    req.onsuccess = () => resolve();
    req.onerror = () => reject(req.error);
  });
}

async function ensureReadPermission(dir: FileSystemDirectoryHandle) {
  const q = await (dir as any).queryPermission?.({ mode: "read" });
  if (q === "granted") return;
  const r = await (dir as any).requestPermission?.({ mode: "read" });
  if (r !== "granted") throw new Error("Read permission was not granted for the selected folder.");
}

async function restoreHandle(): Promise<FileSystemDirectoryHandle | null> {
  if (currentDir) return currentDir;
  try {
    const h = await idbGet<FileSystemDirectoryHandle>(KEY);
    if (!h) return null;
    await ensureReadPermission(h);
    currentDir = h;
    return currentDir;
  } catch { return null; }
}

export async function chooseFolder(): Promise<FileSystemDirectoryHandle> {
  if (!("showDirectoryPicker" in window)) {
    throw new Error("Folder picker not supported in this host. Use manual file upload.");
  }
  const dir = await (window as any).showDirectoryPicker({ id: "imports-root", mode: "read" });
  await ensureReadPermission(dir);
  currentDir = dir;
  try { await idbSet(KEY, dir); } catch { /* ignore serialization errors */ }
  return dir;
}

export async function clearSavedFolder() {
  await idbDel(KEY);
  currentDir = null;
}

export async function getSavedFolderName(): Promise<string> {
  const dir = await restoreHandle();
  return dir?.name ?? "(not set)";
}

export async function getFileFromSavedFolder(
  filename: string,
  opts?: { repromptIfMissing?: boolean }
): Promise<File> {
  let dir = await restoreHandle();
  if (!dir) dir = await chooseFolder();

  // Try current folder
  let fh = await dir.getFileHandle(filename).catch(() => null);
  if (fh) return await fh.getFile();

  // Reprompt if missing
  if (opts?.repromptIfMissing !== false) {
    dir = await chooseFolder();
    fh = await dir.getFileHandle(filename).catch(() => null);
    if (fh) return await fh.getFile();
  }

  throw new Error(`Could not find "${filename}" in the chosen folder.`);
}

