// src/taskpane/perfCalculator.ts
import * as XLSX from "xlsx";
import { getFileFromSavedFolder } from "./fsAccess";

/* -------------------- helpers -------------------- */

function norm(s: string): string {
  return String(s || "")
    .toLowerCase()
    .replace(/[^\p{L}\p{N}]+/gu, " ")
    .replace(/\s+/g, " ")
    .trim();
}

function parseExcelDate(v: any): Date | null {
  if (v == null || v === "") return null;
  if (typeof v === "number") {
    // Excel serial date
    const epoch = Date.UTC(1899, 11, 30);
    return new Date(epoch + v * 86400000);
  }
  const t = Date.parse(String(v));
  return isNaN(t) ? null : new Date(t);
}

function endOfMonth(d: Date): Date {
  return new Date(Date.UTC(d.getUTCFullYear(), d.getUTCMonth() + 1, 0));
}

function parseReturn(val: any): number {
  // Treat ALL inputs as whole-percent and convert to decimal.
  // Examples:
  //   1.23      -> 0.0123
  //   "1.23"    -> 0.0123
  //   "1.23%"   -> 0.0123
  //   0         -> 0
  if (val == null || val === "") return 0;

  if (typeof val === "number") return val / 100;

  const s = String(val).trim().replace(/,/g, ".");
  if (s.endsWith("%")) {
    const n = parseFloat(s.slice(0, -1));
    return isFinite(n) ? n / 100 : 0;
  }

  const n = parseFloat(s);
  return isFinite(n) ? n / 100 : 0;
}

/* -------------------- Morningstar matrix reader -------------------- */

type MsMatrix = {
  aoa: any[][];
  headerRow: number;               // row index with dates (zero-based)
  firstDataRow: number;            // first fund row index (zero-based)
  nameCol: number;                 // column index with fund names (usually 0)
  monthCols: { key: string; col: number; date: Date }[]; // ordered
  rowIndex: Record<string, number>; // normalized name → row index
};

let cachedMs: MsMatrix | null = null;

async function loadMorningstarMatrix(): Promise<MsMatrix> {
  if (cachedMs) return cachedMs;

  const file = await getFileFromSavedFolder("Morningstar_Data.xlsx", { repromptIfMissing: true });
  const buf = await file.arrayBuffer();
  const wb = XLSX.read(buf, { type: "array" });
  const ws = wb.Sheets["Sheet1"] ?? wb.Sheets[wb.SheetNames[0]];

  // Convert sheet to AoA rectangle
  const range = XLSX.utils.decode_range(ws["!ref"] || "A1:A1");
  const rows = range.e.r - range.s.r + 1;
  const cols = range.e.c - range.s.c + 1;
  const aoa: any[][] = Array.from({ length: rows }, (_, r) =>
    Array.from({ length: cols }, (_, c) => {
      const cell = ws[XLSX.utils.encode_cell({ r: r + range.s.r, c: c + range.s.c })];
      return cell ? cell.v : "";
    })
  );

  // We expect ROW 8 (1-based) to contain EOM dates → index 7 (0-based).
  // Be tolerant: scan rows 5..12 and pick the one with most valid dates.
  let headerRow = 7;
  let bestCount = -1;
  for (let r = 4; r <= 11 && r < rows; r++) {
    let cnt = 0;
    for (let c = 0; c < cols; c++) {
      if (parseExcelDate(aoa[r][c])) cnt++;
    }
    if (cnt > bestCount && cnt >= 6) {
      bestCount = cnt;
      headerRow = r;
    }
  }

  // Build month columns from the headerRow (dates to the right).
  const monthCols: { key: string; col: number; date: Date }[] = [];
  for (let c = 0; c < cols; c++) {
    const d = parseExcelDate(aoa[headerRow][c]);
    if (!d) continue;
    const eom = endOfMonth(d);
    monthCols.push({
      key: `${eom.getUTCFullYear()}-${eom.getUTCMonth() + 1}`,
      col: c,
      date: eom,
    });
  }
  // Ensure ascending order by date/column
  monthCols.sort((a, b) => a.col - b.col);

  // Name column = first column that has many strings under headerRow
  let nameCol = 0;
  let bestNameCount = -1;
  for (let c = 0; c < Math.min(5, cols); c++) {
    let cnt = 0;
    for (let r = headerRow + 1; r < rows; r++) {
      const v = aoa[r][c];
      if (typeof v === "string" && v.trim()) cnt++;
    }
    if (cnt > bestNameCount) {
      bestNameCount = cnt;
      nameCol = c;
    }
  }

  const firstDataRow = headerRow + 1;

  const rowIndex: Record<string, number> = {};
  for (let r = firstDataRow; r < rows; r++) {
    const name = String(aoa[r][nameCol] ?? "").trim();
    if (!name) continue;
    rowIndex[norm(name)] = r;
  }

  cachedMs = { aoa, headerRow, firstDataRow, nameCol, monthCols, rowIndex };
  return cachedMs;
}

/* -------------------- Public API used by the form -------------------- */

export function endOfMonthPublic(d: Date): Date {
  return endOfMonth(d);
}

export async function totalReturnForFund(
  fundName: string,
  numMonths: number,
  endingEom: Date,
  perfCurrency: string // "ZAR", "USD", "GBP", "EUR"
): Promise<number> {
  const ms = await loadMorningstarMatrix();
  const { aoa, nameCol, monthCols, rowIndex } = ms;

  // --- pick ending month col (latest <= chosen EOM) ---
  let endIdx = -1;
  for (let i = monthCols.length - 1; i >= 0; i--) {
    if (monthCols[i].date.getTime() <= endingEom.getTime()) {
      endIdx = i;
      break;
    }
  }
  if (endIdx === -1) return 0;

  // --- locate fund row (fuzzy) ---
  const simp = (s: string) =>
    norm(s)
      .replace(/\(.*?\)/g, "")
      .replace(/[-–—]/g, " ")
      .replace(/\s+/g, " ")
      .trim();

  const wanted = simp(fundName);
  let row = rowIndex[wanted];
  if (row == null) {
    // try contains / contained-in
    const keys = Object.keys(rowIndex);
    let bestKey = "";
    for (const k of keys) {
      if (k.includes(wanted) || wanted.includes(k)) {
        bestKey = k;
        break;
      }
    }
    if (bestKey) row = rowIndex[bestKey];
  }
  if (row == null) return 0;

  // --- currency row (optional) ---
  let currRow: number | undefined;
  if (perfCurrency && perfCurrency.toUpperCase() !== "ZAR") {
    const cname = norm(`Currency - ${perfCurrency.toUpperCase()}`);
    currRow = rowIndex[cname];
  }

  // --- compound last N months ---
  let prod = 1;
  const take = Math.max(1, numMonths);
  for (let i = 0; i < take && endIdx - i >= 0; i++) {
    const col = monthCols[endIdx - i].col;
    const rFund = parseReturn(aoa[row][col]);

    let r = rFund;
    if (currRow != null) {
      const rFx = parseReturn(aoa[currRow][col]);
      // Assumption: currency series is the conversion factor to move ZAR returns
      // into the selected currency.
      r = (1 + rFund) / (1 + rFx) - 1;
    }
    prod *= 1 + r;
  }

  return prod - 1;
}

export function portfolioReturn(weights: number[], returns: number[]): number {
  let wsum = 0, acc = 0;
  for (let i = 0; i < weights.length; i++) {
    const w = Number(weights[i] ?? 0);
    const r = Number(returns[i] ?? 0);
    if (!isFinite(w)) continue;
    wsum += w;
    acc += w * r;
  }
  return wsum ? acc / wsum : 0;
}

/* -------------------- Universes from Investment_Options.xlsx -------------------- */

import { loadInvestmentUniverses as loadU, loadInvestmentOptions as loadO } from "./perfInvestments";
export const loadInvestmentUniverses = loadU;
export const loadInvestmentOptions = loadO;


// Returns the latest end-of-month date found in Morningstar_Data.xlsx.
// Fallback = last EOM relative to today.
export async function getLatestAvailableEom(): Promise<Date> {
  // Reuse the cached Morningstar matrix loader already in this file
  // (loadMorningstarMatrix was defined earlier in perfCalculator.ts)
  // If your file name/sheet name change, keep loadMorningstarMatrix in sync.
  // @ts-ignore: using internal helper from same file
  const ms = await (loadMorningstarMatrix as any)();
  if (ms?.monthCols?.length) {
    return ms.monthCols[ms.monthCols.length - 1].date as Date;
  }
  const now = new Date();
  return new Date(Date.UTC(now.getUTCFullYear(), now.getUTCMonth() + 1, 0));
}
