// src/taskpane/ui/importerForm.ts
import * as XLSX from "xlsx";
import { chooseFolder, getSavedFolderName, getFileFromSavedFolder } from "../fsAccess";
import { DataLink, getEffectiveLinks, loadLinksFromFile, writeLinksTableToWorkbook } from "../dataLinks";

type ImporterOpts = { onBack?: () => void };

function arrayBufferToBase64(buf: ArrayBuffer): string {
  let binary = "";
  const bytes = new Uint8Array(buf);
  const chunk = 0x8000;
  for (let i = 0; i < bytes.length; i += chunk) {
    binary += String.fromCharCode(...bytes.subarray(i, i + chunk));
  }
  return btoa(binary);
}

export function renderImporter(mountId: string, opts?: ImporterOpts) {
  const root = document.getElementById(mountId)!;
  root.innerHTML = `
    <div class="topbar">
      <button class="back" id="imp-back" title="Back">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
          <path fill-rule="evenodd" d="M12.79 5.23a.75.75 0 01-.02 1.06L8.832 10l3.938 3.71a.75.75 0 11-1.04 1.08l-4.5-4.25a.75.75 0 010-1.08l4.5-4.25a.75.75 0 011.06.02z" clip-rule="evenodd" />
        </svg>
      </button>
      <h2>G&S Data Importer</h2>
      <span></span>
    </div>

    <div class="container">
      <div class="card">
        <h3>Import Documents</h3>
        <div class="muted">
          Generated from <b>Data_Links.xlsx</b>. The entire source sheet will be pasted to the destination sheet.
        </div>

        <div id="btnZone"></div>

        <hr />

        <div style="display:flex; gap:8px; flex-wrap:wrap">
          <button class="btn secondary" id="btn-folder">Change Folder…</button>
          <button class="btn secondary" id="btn-refresh">Refresh</button>
        </div>
        <div id="folderName" class="muted" style="margin-top: 8px;"></div>

        <hr />

        <div>
          <button class="btn secondary" id="btn-writeTable">Refresh 'Data_Links' Table</button>
          <div class="muted" style="font-size:12px; margin-top:6px">
            Update the in-workbook table on sheet <b>Uploader</b> at <b>A20</b>.
          </div>
        </div>

        <p id="status" class="muted"></p>
      </div>
    </div>
  `;

  (root.querySelector("#imp-back") as HTMLButtonElement).onclick = () => opts?.onBack?.();

  const setStatus = (t: string) => {
    const statusEl = root.querySelector("#status") as HTMLElement;
    statusEl.textContent = t;
    statusEl.style.marginTop = t ? "16px" : "0";
  };
  const folderLabel = root.querySelector("#folderName") as HTMLElement;

  async function refreshFolderLabel() {
    folderLabel.textContent = `Folder: ${await getSavedFolderName()}`;
  }

  (root.querySelector("#btn-writeTable") as HTMLButtonElement).onclick = async () => {
    try {
      setStatus("Writing Data_Links table to workbook…");
      const links = await loadLinksFromFile();
      await writeLinksTableToWorkbook(links);
      setStatus("Data_Links table refreshed.");
    } catch (e: any) {
      setStatus(`Error: ${e?.message || String(e)}`);
    }
  };

  (root.querySelector("#btn-refresh") as HTMLButtonElement).onclick = async () => {
    try {
      await renderRows();
      setStatus("Links refreshed.");
    } catch (e: any) {
      setStatus(`Error refreshing links: ${e?.message || String(e)}`);
    }
  };

  (root.querySelector("#btn-folder") as HTMLButtonElement).onclick = async () => {
    try {
      await chooseFolder();
      await refreshFolderLabel();
      await renderRows();
      setStatus("Folder updated.");
    } catch (e: any) {
      setStatus(`Error selecting folder: ${e?.message || String(e)}`);
    }
  };

  const invalidChars = /[:\\/\?\*\[\]]/;
  function validateSheetName(name: string): string | null {
    const n = name.trim();
    if (!n) return "Destination is required.";
    if (n.length > 31) return "Sheet name must be 31 characters or fewer.";
    if (invalidChars.test(n)) return "Sheet name cannot contain : \\ / ? * [ ]";
    return null;
  }

  function buildRow(link: DataLink): HTMLDivElement {
    const row = document.createElement("div");
    row.className = "row";

    const defaultDest = (link.inputSheetName || "").trim();

    row.innerHTML = `
      <div class="row-details">
        <div class="name">${link.buttonName}</div>
        <div class="path muted">
          From: <b>${link.documentName}</b> — ${link.sourceSheet || "(first sheet)"}
          <br />
          To: <b><span class="toName">${defaultDest || "(not set)"}</span></b>
        </div>
      </div>
      <div class="row-actions">
        <input type="text" class="override-dest" placeholder="Override destination…" />
        <button class="btn primary import-btn">Import</button>
      </div>
    `;

    const overrideInput = row.querySelector(".override-dest") as HTMLInputElement;
    const importBtn = row.querySelector(".import-btn") as HTMLButtonElement;
    const toName = row.querySelector(".toName") as HTMLElement;

    const updateButtonState = () => {
      const dest = overrideInput.value.trim() || defaultDest;
      const err = validateSheetName(dest);
      importBtn.disabled = !!err;
      importBtn.title = err || "";
      toName.textContent = dest || "(not set)";
    };

    overrideInput.addEventListener("input", updateButtonState);
    updateButtonState();

    importBtn.onclick = async () => {
      const dest = overrideInput.value.trim() || defaultDest;
      const err = validateSheetName(dest);
      if (err) {
        setStatus(`Cannot import "${link.buttonName}": ${err}`);
        return;
      }
      await runImportLink(link, dest);
    };

    return row;
  }

  async function renderRows() {
    const zone = root.querySelector("#btnZone") as HTMLDivElement;
    zone.innerHTML = "";
    setStatus("Loading links…");
    let links: DataLink[] = [];
    try {
      links = await getEffectiveLinks();
    } catch (e: any) {
      setStatus(`Could not read Data_Links.xlsx: ${e?.message || e}`);
      return;
    }

    if (!links.length) {
      zone.innerHTML = `<div class="muted">No importable documents found in Data_Links.xlsx.</div>`;
      setStatus("");
      return;
    }

    for (const link of links) {
      zone.appendChild(buildRow(link));
    }
    setStatus("");
  }

  async function runImportLink(link: DataLink, destinationSheet: string) {
    try {
      setStatus(`Importing "${link.buttonName}" → ${destinationSheet}…`);
      const file = await getFileFromSavedFolder(link.documentName, { repromptIfMissing: true });
      const buf = await file.arrayBuffer();
      const wb = XLSX.read(buf, { type: "array" });
      const sourceName = (link.sourceSheet && wb.Sheets[link.sourceSheet])
        ? link.sourceSheet
        : wb.SheetNames[0];
      const base64 = arrayBufferToBase64(buf);

      await Excel.run(async (ctx) => {
        const wsColl = ctx.workbook.worksheets;
        wsColl.load("items/name");
        await ctx.sync();

        const dest = wsColl.getItemOrNullObject(destinationSheet);
        await ctx.sync();
        if (!dest.isNullObject) {
          dest.delete();
          await ctx.sync();
        }

        const canAddFromBase64 = typeof (wsColl as any).addFromBase64 === "function";
        if (canAddFromBase64) {
          const before = new Set(wsColl.items.map(w => w.name));
          // @ts-ignore
          (wsColl as any).addFromBase64(base64, { sheetNamesToInsert: [sourceName], positionType: "End" });
          await ctx.sync();

          wsColl.load("items/name");
          await ctx.sync();

          const after = wsColl.items.map(w => w.name);
          const importedName = after.find(n => !before.has(n)) || sourceName;
          let imported = wsColl.getItem(importedName);
          imported.name = destinationSheet;
          imported.activate();
          await ctx.sync();
        } else {
          const ws = wb.Sheets[sourceName];
          const aoa = XLSX.utils.sheet_to_json(ws, { header: 1, raw: true, blankrows: false }) as any[][];
          const cols = Math.max(1, ...aoa.map(r => (Array.isArray(r) ? r.length : 0)));
          const padded = aoa.map(r => {
            const row = Array.isArray(r) ? r.slice() : [];
            while (row.length < cols) row.push("");
            return row;
          });

          let target = wsColl.add(destinationSheet);
          const rng = target.getRangeByIndexes(0, 0, Math.max(1, padded.length), Math.max(1, cols));
          rng.values = padded.length ? padded : [[""]];
          target.activate();
          await ctx.sync();
        }
      });

      setStatus(`Successfully imported "${link.buttonName}" to sheet "${destinationSheet}".`);
    } catch (e: any) {
      setStatus(`Error: ${e?.message || String(e)}`);
    }
  }

  // init
  refreshFolderLabel();
  renderRows();
}