// src/taskpane/ui/start.ts
export function renderStart(
  mountId: string,
  opts: { onOpenImporter: () => void; onOpenCalculator: () => void }
) {
  const root = document.getElementById(mountId)!;
  root.innerHTML = `
    <div class="topbar">
      <h2>G&S Addin</h2>
    </div>
    <div class="container">
      <div class="card">
        <h3>Welcome</h3>
        <div class="muted">Choose a tool below to get started.</div>
      </div>

      <div class="card" style="margin-top: 16px;">
        <h3>Tools</h3>
        <div id="tools-list"></div>
      </div>
    </div>
  `;

  const toolsList = root.querySelector("#tools-list")!;

  const importerCard = document.createElement("div");
  importerCard.className = "row";
  importerCard.innerHTML = `
    <div class="row-details">
      <div class="name">Documents Importer</div>
      <div class="path muted">Pull data from your local folder into your workbook.</div>
    </div>
    <div class="row-actions">
      <button class="btn primary" id="open-importer">Open</button>
    </div>
  `;
  (importerCard.querySelector("#open-importer") as HTMLButtonElement).onclick = opts.onOpenImporter;

  const calculatorCard = document.createElement("div");
  calculatorCard.className = "row";
  calculatorCard.innerHTML = `
    <div class="row-details">
      <div class="name">Fund Performance Calculator</div>
      <div class="path muted">Calculate single-fund and portfolio returns.</div>
    </div>
    <div class="row-actions">
      <button class="btn primary" id="open-calculator">Open</button>
    </div>
  `;
  (calculatorCard.querySelector("#open-calculator") as HTMLButtonElement).onclick = opts.onOpenCalculator;

  toolsList.appendChild(importerCard);
  toolsList.appendChild(calculatorCard);
}