// src/taskpane/dataLinks.ts
import * as XLSX from "xlsx";
import { getFileFromSavedFolder } from "./fsAccess";

export type DataLink = {
  documentName: string;      // source file name (e.g., "MS Datadump - Latest.xlsx")
  sourceSheet: string;       // source worksheet inside that file
  buttonName: string;        // label shown on the Import button
  inputSheetName: string;    // DESTINATION sheet in the CURRENT workbook
};

const norm = (s: any) =>
  String(s ?? "")
    .toLowerCase()
    .replace(/[^\p{L}\p{N}]+/gu, " ")
    .replace(/\s+/g, " ")
    .trim();

function idxOfHeader(H: string[], candidates: string[]): number {
  const C = candidates.map(norm);
  for (const c of C) {
    const i = H.findIndex(h => h === c);
    if (i !== -1) return i;
  }
  return -1;
}

/** Find a header row anywhere in the first ~50 rows of a sheet. */
function findHeaderIndexes(rows: any[][]) {
  for (let r = 0; r < Math.min(rows.length, 50); r++) {
    const header = (rows[r] || []).map(String);
    const H = header.map(norm);
    const iDoc   = idxOfHeader(H, ["document name", "document", "file", "name of the document", "source file"]);
    const iSheet = idxOfHeader(H, ["sheet name", "source sheet"]);
    const iBtn   = idxOfHeader(H, ["button name", "button", "action"]);
    const iDest  = idxOfHeader(H, ["destination sheet name"]);
    if (iDoc !== -1 && iBtn !== -1 && iDest !== -1) {
      return { row: r, iDoc, iSheet, iBtn, iDest };
    }
  }
  return null;
}

/** Try to parse links from one worksheet (header can be below row 1). */
function parseLinksFromWorksheet(ws: XLSX.WorkSheet): DataLink[] | null {
  const rows = XLSX.utils.sheet_to_json(ws, { header: 1, raw: true }) as any[][];
  if (!rows.length) return null;

  const hdr = findHeaderIndexes(rows);
  if (!hdr) return null;

  const out: DataLink[] = [];
  for (let r = hdr.row + 1; r < rows.length; r++) {
    const row = rows[r] || [];
    const doc   = String(row[hdr.iDoc]   ?? "").trim();
    const src   = String(row[hdr.iSheet] ?? "").trim();
    const btn   = String(row[hdr.iBtn]   ?? "").trim();
    const dest  = String(row[hdr.iDest]  ?? "").trim(); // DESTINATION ONLY – no fallback
    if (!doc || !btn) continue;
    out.push({ documentName: doc, sourceSheet: src, buttonName: btn, inputSheetName: dest });
  }
  return out.length ? out : null;
}

/** Load rows from Data_Links.xlsx by scanning all sheets and picking the first
 *  that actually contains a destination column. No guessing.
 */
export async function loadLinksFromFile(): Promise<DataLink[]> {
  const file = await getFileFromSavedFolder("Data_Links.xlsx", { repromptIfMissing: true });
  const buf = await file.arrayBuffer();
  const wb = XLSX.read(buf, { type: "array" });

  let bestEffort: DataLink[] | null = null;

  for (const name of wb.SheetNames) {
    const ws = wb.Sheets[name];
    const parsed = parseLinksFromWorksheet(ws);
    if (!parsed) continue;

    // If a sheet has a destination column, it's the best one, so use it.
    const firstRow = parsed[0];
    if (firstRow && firstRow.inputSheetName) {
      return parsed;
    }

    // Otherwise, save it as a fallback in case no better sheet is found.
    if (!bestEffort) {
      bestEffort = parsed;
    }
  }

  return bestEffort || [];
}

/** Write/refresh the editable table in the workbook (sheet "Uploader" at A20). */
export async function writeLinksTableToWorkbook(links: DataLink[]) {
  await Excel.run(async (ctx) => {
    let ws = ctx.workbook.worksheets.getItemOrNullObject("Uploader");
    await ctx.sync();
    if (ws.isNullObject) ws = ctx.workbook.worksheets.add("Uploader");

    const tables = ws.tables;
    tables.load("items/name");
    await ctx.sync();
    const existing = tables.items.find(t => t.name === "Data_Links");
    if (existing) existing.delete();

    const headers = [[
      "Document Name",
      "Sheet Name",
      "Button Name",
      "Destination Sheet Name" // <-- matches your file
    ]];
    const body = links.map(l => [l.documentName, l.sourceSheet, l.buttonName, l.inputSheetName]);
    const all = headers.concat(body);

    const startRow = 19, startCol = 0; // A20
    const range = ws.getRangeByIndexes(startRow, startCol, all.length, 4);
    range.values = all;

    const table = ws.tables.add(range.getAddress(), true);
    table.name = "Data_Links";
    table.getHeaderRowRange().format.fill.color = "#eef2ff";
    table.getHeaderRowRange().format.font.bold = true;

    await ctx.sync();
  });
}

/** Read overrides from the in-workbook table (same header name). */
export async function readLinksTableFromWorkbook(): Promise<DataLink[] | null> {
  return Excel.run(async (ctx) => {
    const ws = ctx.workbook.worksheets.getItemOrNullObject("Uploader");
    await ctx.sync();
    if (ws.isNullObject) return null;

    const table = ws.tables.getItemOrNullObject("Data_Links");
    await ctx.sync();
    if (table.isNullObject) return null;

    const rng = table.getRange();
    rng.load("values");
    await ctx.sync();
    const vals = rng.values as any[][];
    if (!vals.length) return [];

    const header = vals[0].map(String);
    const H = header.map(norm);

    const iDoc   = idxOfHeader(H, ["document name", "document", "file", "name of the document", "source file"]);
    const iSheet = idxOfHeader(H, ["sheet name", "source sheet"]);
    const iBtn   = idxOfHeader(H, ["button name", "button", "action"]);
    const iDest  = idxOfHeader(H, ["destination sheet name"]);
    const out: DataLink[] = [];
    for (let r = 1; r < vals.length; r++) {
      const row = vals[r] || [];
      const doc   = String(row[iDoc]   ?? "").trim();
      const src   = String(row[iSheet] ?? "").trim();
      const btn   = String(row[iBtn]   ?? "").trim();
      const dest  = String(row[iDest]  ?? "").trim();
      if (!doc || !btn) continue;
      out.push({ documentName: doc, sourceSheet: src, buttonName: btn, inputSheetName: dest });
    }
    return out;
  });
}

/** Merge defaults with in-workbook overrides; DESTINATION (inputSheetName) from the table wins when present. */
export async function getEffectiveLinks(): Promise<DataLink[]> {
  const defaults  = await loadLinksFromFile();
  const fromTable = await readLinksTableFromWorkbook();

  if (!fromTable || !fromTable.length) return defaults;

  const key = (d: DataLink) => norm(d.buttonName);
  const map = new Map<string, DataLink>(defaults.map(d => [key(d), d]));

  for (const row of fromTable) {
    const k = key(row);
    const base = map.get(k);
    if (base) {
      map.set(k, { ...base, inputSheetName: row.inputSheetName || base.inputSheetName });
    } else {
      map.set(k, row);
    }
  }
  return Array.from(map.values());
}