// src/taskpane/perfForm.ts
import {
  totalReturnForFund,
  portfolioReturn,
  endOfMonthPublic as endOfMonth,
  loadInvestmentOptions,
  loadInvestmentUniverses,
} from "./perfCalculator";

type Row = { fund: HTMLInputElement; weight: HTMLInputElement; ret: HTMLInputElement };

export async function renderPerfForm(
  mountId = "app",
  opts?: { onBack?: () => void }
) {
  const mount = document.getElementById(mountId)!;
  mount.innerHTML = `
    <div class="topbar">
      <button class="back" id="pf-back">◀ Back</button>
      <h2>Fund Performance Calculator</h2>
      <span></span>
    </div>

    <div class="container">
      <!-- Controls -->
      <div class="card" style="padding-bottom:6px">
        <div style="display:grid;grid-template-columns:repeat(4,minmax(0,1fr));gap:8px;align-items:end">
          <label>Return Month (EOM)<br/><input id="pf-returnDate" type="month"></label>
          <label>Months<br/><input id="pf-numMonths" type="number" min="1" max="120" value="1"></label>
          <label>Currency<br/>
            <select id="pf-currency">
              <option>ZAR</option>
              <option>USD</option>
              <option>GBP</option>
              <option>EUR</option>
            </select>
          </label>
          <div>
            <button class="btn" id="pf-calc">Calculate</button>
            <button class="btn secondary" id="pf-clearAll" style="margin-left:6px">Clear all</button>
          </div>
        </div>
      </div>

      <!-- Performance table -->
      <div class="card">
        <div style="overflow:auto">
          <table id="pf-table" style="width:100%;border-collapse:collapse;table-layout:fixed;">
            <colgroup>
              <col style="width:36px" />    <!-- # -->
              <col />                       <!-- Fund (flex) -->
              <col style="width:80px" />    <!-- Weight -->
              <col style="width:100px" />   <!-- Return -->
              <col style="width:90px" />    <!-- Actions -->
            </colgroup>
            <thead>
              <tr>
                <th style="text-align:left;border-bottom:1px solid #eee">#</th>
                <th style="text-align:left;border-bottom:1px solid #eee">Fund</th>
                <th style="text-align:right;border-bottom:1px solid #eee">Weight</th>
                <th style="text-align:right;border-bottom:1px solid #eee">Return</th>
                <th style="text-align:center;border-bottom:1px solid #eee">Actions</th>
              </tr>
            </thead>
            <tbody></tbody>
          </table>
        </div>
        <div style="margin-top:10px;font-weight:600">Portfolio Return: <span id="pf-portRet">–</span></div>
      </div>

      <!-- Universe below the table -->
      <div class="card">
        <div style="display:grid;gap:8px">
          <div style="display:grid;grid-template-columns:1fr 1fr;gap:8px">
            <label>Universe<br/>
              <select id="pf-universe"></select>
            </label>
            <label>Search<br/>
              <input id="pf-search" type="text" placeholder="Type to filter funds…" style="width:100%">
            </label>
          </div>

          <div class="muted" style="font-size:12px">
            Tip: <b>double-click</b> a fund in the list below to add it to the next empty row.
          </div>

          <div id="pf-fundlist" style="border:1px solid #eee;border-radius:10px;max-height:260px;overflow:auto"></div>
        </div>
      </div>
    </div>
  `;

  // Back nav
  (mount.querySelector("#pf-back") as HTMLButtonElement).onclick = () => opts?.onBack?.();

  // Build rows
  const tbody = mount.querySelector("tbody")!;
  const rows: Row[] = [];
  for (let i = 1; i <= 10; i++) {
    const tr = document.createElement("tr");
    tr.innerHTML = `
      <td style="padding:4px 0">${i}</td>
      <td style="padding:4px 0">
        <input id="pf-fund-${i}" type="text"
               style="width:100%;box-sizing:border-box">
      </td>
      <td style="padding:4px 0;text-align:right">
        <input id="pf-weight-${i}" type="number" step="any" min="0"
               style="width:72px;text-align:right;box-sizing:border-box">
      </td>
      <td style="padding:4px 0;text-align:right">
        <input id="pf-return-${i}" type="text" readonly
               style="width:92px;text-align:right;background:#f8f8f8;box-sizing:border-box">
      </td>
      <td style="padding:4px 0;text-align:center">
        <button class="btn secondary" data-clear="${i}" title="Clear row" style="padding:6px 10px">Clear</button>
      </td>
    `;
    tbody.appendChild(tr);
    rows.push({
      fund: tr.querySelector(`#pf-fund-${i}`)! as HTMLInputElement,
      weight: tr.querySelector(`#pf-weight-${i}`)! as HTMLInputElement,
      ret: tr.querySelector(`#pf-return-${i}`)! as HTMLInputElement,
    });
  }

  // UI refs
  const $ = (sel: string) => mount.querySelector(sel)!;
  const calcBtn = $("#pf-calc") as HTMLButtonElement;
  const clearAllBtn = $("#pf-clearAll") as HTMLButtonElement;
  const portRetSpan = $("#pf-portRet") as HTMLSpanElement;
  const universeSel = $("#pf-universe") as HTMLSelectElement;
  const searchBox = $("#pf-search") as HTMLInputElement;
  const fundListDiv = $("#pf-fundlist") as HTMLDivElement;
  const numMonthsInput = $("#pf-numMonths") as HTMLInputElement;
  const currencySel = $("#pf-currency") as HTMLSelectElement;
  const returnMonthInput = $("#pf-returnDate") as HTMLInputElement;

  // Default Return Month = previous month's EOM
  {
    const now = new Date();
    const prevEom = new Date(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), 0));
    const ym = `${prevEom.getUTCFullYear()}-${String(prevEom.getUTCMonth() + 1).padStart(2, "0")}`;
    returnMonthInput.value = ym;
  }

  // Helpers for marking selected funds
  const norm = (s: string) => s.trim().toLowerCase();
  function selectedFundsSet(): Set<string> {
    const set = new Set<string>();
    rows.forEach(r => {
      const v = norm(r.fund.value);
      if (v) set.add(v);
    });
    return set;
  }

  let currentUniverseItems: string[] = [];
  function getFilteredItems(): string[] {
    const q = searchBox.value.trim().toLowerCase();
    return q ? currentUniverseItems.filter(x => x.toLowerCase().includes(q)) : currentUniverseItems.slice();
  }

  function renderFundListWithMarks() {
    const items = getFilteredItems();
    const selected = selectedFundsSet();

    // Preserve scroll position
    const scrollTop = fundListDiv.scrollTop;
    fundListDiv.innerHTML = "";

    if (!items.length) {
      fundListDiv.innerHTML = `<div class="muted" style="padding:10px">No funds to display.</div>`;
      return;
    }

    const ul = document.createElement("ul");
    ul.style.listStyle = "none";
    ul.style.padding = "6px";
    ul.style.margin = "0";

    items.forEach((name) => {
      const li = document.createElement("li");
      li.style.padding = "8px 10px";
      li.style.cursor = "pointer";
      li.style.borderRadius = "8px";
      li.addEventListener("mouseenter", () => (li.style.background = "#f5f5f7"));
      li.addEventListener("mouseleave", () => (li.style.background = ""));

      const row = document.createElement("div");
      row.style.display = "flex";
      row.style.alignItems = "center";
      row.style.justifyContent = "space-between";
      row.style.gap = "8px";

      const left = document.createElement("span");
      left.textContent = name;

      const isSelected = selected.has(norm(name));
      if (isSelected) {
        const badge = document.createElement("span");
        badge.textContent = "✓ Added";
        badge.style.fontSize = "12px";
        badge.style.color = "#065f46";
        badge.style.background = "#ecfdf5";
        badge.style.border = "1px solid #a7f3d0";
        badge.style.padding = "2px 6px";
        badge.style.borderRadius = "999px";
        row.appendChild(left);
        row.appendChild(badge);
      } else {
        row.appendChild(left);
      }

      li.appendChild(row);

      // Double-click to add to next empty row
      li.addEventListener("dblclick", () => {
        for (const r of rows) {
          if (!r.fund.value.trim()) {
            r.fund.value = name;
            break;
          }
        }
        renderFundListWithMarks(); // refresh badges after adding
      });

      ul.appendChild(li);
    });

    fundListDiv.appendChild(ul);
    fundListDiv.scrollTop = scrollTop; // restore scroll
  }

  // Row clear button (also refresh marks)
  tbody.addEventListener("click", (ev) => {
    const t = ev.target as HTMLElement;
    const idx = t.getAttribute("data-clear");
    if (!idx) return;
    const i = Number(idx) - 1;
    rows[i].fund.value = "";
    rows[i].weight.value = "";
    rows[i].ret.value = "";
    renderFundListWithMarks();
  });

  // Also refresh marks when user types/edits the fund cell
  rows.forEach(r => {
    r.fund.addEventListener("input", renderFundListWithMarks);
    r.fund.addEventListener("change", renderFundListWithMarks);
  });

  // Universes (default to All) + list loading
  async function populateUniverses() {
    universeSel.innerHTML = "";
    const universes = await loadInvestmentUniverses(); // ["All", ...]
    for (const name of universes) {
      const opt = document.createElement("option");
      opt.value = name;
      opt.textContent = name;
      universeSel.appendChild(opt);
    }
    if (Array.from(universeSel.options).some(o => o.value === "All")) {
      universeSel.value = "All";
    }
  }

  async function loadUniverseItems() {
    const name = universeSel.value;
    currentUniverseItems = await loadInvestmentOptions(name);
    renderFundListWithMarks();
  }

  searchBox.addEventListener("input", renderFundListWithMarks);

  await populateUniverses();
  await loadUniverseItems();
  universeSel.addEventListener("change", loadUniverseItems);

  // Calculate
  calcBtn.addEventListener("click", async () => {
    try {
      calcBtn.disabled = true;
      portRetSpan.textContent = "…";

      const [y, m] = returnMonthInput.value.split("-").map(Number);
      if (!y || !m) throw new Error("Please choose a Return Month.");
      const endingEom = endOfMonth(new Date(Date.UTC(y, m - 1, 1)));
      const months = Math.max(1, Number(numMonthsInput.value || 1));
      const perfCurr = currencySel.value;

      const weights: number[] = [];
      const rets: number[] = [];

      for (const r of rows) {
        const fund = r.fund.value.trim();
        if (!fund) { r.ret.value = ""; continue; }

        const tr = await totalReturnForFund(fund, months, endingEom, perfCurr);
        r.ret.value = `${((Number.isFinite(tr) ? tr : 0) * 100).toFixed(2)}%`;

        weights.push(Number(r.weight.value || 0));
        rets.push(Number.isFinite(tr) ? tr : 0);
      }

      const pr = portfolioReturn(weights, rets);
      portRetSpan.textContent = `${((Number.isFinite(pr) ? pr : 0) * 100).toFixed(2)}%`;
    } catch (e: any) {
      alert(e?.message || String(e));
    } finally {
      calcBtn.disabled = false;
    }
  });

  // Clear all (also refresh marks)
  (clearAllBtn).addEventListener("click", () => {
    rows.forEach(r => { r.fund.value = ""; r.weight.value = ""; r.ret.value = ""; });
    portRetSpan.textContent = "–";
    renderFundListWithMarks();
  });
}

