// src/taskpane/perfInvestments.ts
import * as XLSX from "xlsx";
import { getFileFromSavedFolder } from "./fsAccess";

function sheetToRectAoA(ws: XLSX.WorkSheet): any[][] {
  const range = XLSX.utils.decode_range(ws["!ref"] || "A1:A1");
  const out: any[][] = [];
  for (let r = range.s.r; r <= range.e.r; r++) {
    const row: any[] = [];
    for (let c = range.s.c; c <= range.e.c; c++) {
      const cell = ws[XLSX.utils.encode_cell({ r: r + range.s.r, c: c + range.s.c })];
      row.push(cell ? cell.v : "");
    }
    out.push(row);
  }
  return out;
}

function norm(s: string): string {
  return String(s || "")
    .toLowerCase()
    .replace(/[^\p{L}\p{N}]+/gu, " ")
    .replace(/\s+/g, " ")
    .trim();
}

type UniverseLoc = { sheet: string; col: number; headerRow: number };

let cachedMap: Record<string, UniverseLoc> | null = null;
let cachedOrder: string[] | null = null;
let cachedAllItems: string[] | null = null;

async function buildIndex(): Promise<void> {
  const file = await getFileFromSavedFolder("Investment_Options.xlsx", { repromptIfMissing: true });
  const buf = await file.arrayBuffer();
  const wb = XLSX.read(buf, { type: "array" });

  const map: Record<string, UniverseLoc> = {};
  const order: string[] = [];
  const seen = new Set<string>();
  const allItems = new Set<string>();

  for (const sheet of wb.SheetNames) {
    const ws = wb.Sheets[sheet];
    if (!ws) continue;
    const aoa = sheetToRectAoA(ws);
    const rows = aoa.length;
    const cols = (aoa[0]?.length ?? 0);

    // Prefer row 2 headers, but tolerate rows 1–5
    const headerRows = [1, 0, 2, 3, 4];

    for (let c = 0; c < cols; c++) {
      let headerRow = -1;
      let name = "";

      for (const hr of headerRows) {
        const v = String(aoa[hr]?.[c] ?? "").trim();
        if (!v) continue;

        // Needs at least two items beneath the header to count as a list
        let cnt = 0;
        for (let r = hr + 1; r < rows; r++) {
          if (String(aoa[r]?.[c] ?? "").trim()) { cnt++; if (cnt >= 2) break; }
        }
        if (cnt >= 2) { headerRow = hr; name = v; break; }
      }

      if (headerRow === -1) continue;

      const key = norm(name);
      if (!seen.has(key)) {
        seen.add(key);
        map[key] = { sheet, col: c, headerRow };
        order.push(name);
      }

      // Aggregate "All" while we're here
      for (let r = headerRow + 1; r < rows; r++) {
        const v = String(aoa[r]?.[c] ?? "").trim();
        if (v) allItems.add(v);
      }
    }
  }

  cachedMap = map;
  cachedOrder = order;
  cachedAllItems = Array.from(allItems);
}

export async function loadInvestmentUniverses(): Promise<string[]> {
  if (!cachedMap) await buildIndex();
  // Always put "All" first
  return ["All", ...(cachedOrder ?? [])];
}

export async function loadInvestmentOptions(universeName: string): Promise<string[]> {
  if (!cachedMap) await buildIndex();

  if (norm(universeName) === "all") {
    return (cachedAllItems ?? []).slice();
  }

  const loc = (cachedMap ?? {})[norm(universeName)];
  if (!loc) return [];

  const file = await getFileFromSavedFolder("Investment_Options.xlsx", { repromptIfMissing: true });
  const buf = await file.arrayBuffer();
  const wb = XLSX.read(buf, { type: "array" });
  const ws = wb.Sheets[loc.sheet];
  if (!ws) return [];
  const aoa = sheetToRectAoA(ws);

  const out: string[] = [];
  const seen = new Set<string>();
  for (let r = loc.headerRow + 1; r < aoa.length; r++) {
    const v = String(aoa[r]?.[loc.col] ?? "").trim();
    if (!v) continue;
    if (!seen.has(v)) { seen.add(v); out.push(v); }
  }
  return out;
}

