// src/taskpane/taskpane.ts
import { renderStart } from "./ui/start";
import { renderImporter } from "./ui/importerForm";
import { renderPerfForm } from "./perfForm";

type View = "home" | "importer" | "calculator";

function mountId() { return "app-container"; }

function go(view: View) {
  if (view === "home") {
    renderStart(mountId(), {
      onOpenImporter: () => go("importer"),
      onOpenCalculator: () => go("calculator"),
    });
  } else if (view === "importer") {
    renderImporter(mountId(), { onBack: () => go("home") });
  } else if (view === "calculator") {
    renderPerfForm(mountId(), { onBack: () => go("home") });
  }
}

Office.onReady(() => {
  go("home");
});
